// eslint-disable-next-line no-restricted-imports
import {on} from 'delegated-events'
import {remoteForm} from '@github/remote-form'

function setIndicator(className: string) {
  const target = document.querySelector<HTMLElement>('.js-status-active')
  for (const statusIndicator of document.querySelectorAll('.js-status-indicator')) {
    statusIndicator.classList.remove('status-indicator-success', 'status-indicator-loading', 'status-indicator-failed')
  }
  if (target instanceof HTMLElement) target.classList.add(className)
}

remoteForm('.js-usage-notification-toggle', async function (form, wants) {
  setIndicator('status-indicator-loading')
  try {
    await wants.text()
    setIndicator('status-indicator-success')
  } catch (error) {
    setIndicator('status-indicator-failed')
  }
})

function setMarker(target: Element) {
  for (const node of document.querySelectorAll('.js-status-indicator')) {
    node.classList.remove('js-status-active')
  }
  target.classList.add('js-status-active')
}

on('change', 'input[type=checkbox][name$="_usage_notification"]', function ({currentTarget}) {
  if (!(currentTarget instanceof HTMLInputElement)) return

  const targetIndicator = currentTarget.closest('.form-checkbox')?.querySelector<HTMLElement>('.js-status-indicator')
  if (targetIndicator instanceof HTMLElement) setMarker(targetIndicator)
})
