import {controller, target} from '@github/catalyst'

// The file size is in bytes, so 10MB
const CERT_EXEMPT_MAX_FILE_SIZE = 10 * 1000 * 1000

@controller
class CertificateUploadCreateElement extends HTMLElement {
  @target formElement: HTMLFormElement
  @target file: HTMLInputElement
  @target uploadBtn: HTMLButtonElement
  @target uploadBtnText: HTMLElement
  @target processingAnime: HTMLElement
  @target errorText: HTMLElement

  connectedCallback() {
    this.processingAnime.style.display = 'none'
    this.errorText.style.display = 'none'
  }

  disconnectedCallback() {
    this.uploadBtn.disabled = false
    this.uploadBtnText.textContent = this.uploadBtnText.getAttribute('data-text')
    this.processingAnime.style.display = 'none'
    this.errorText.style.display = 'none'
  }

  sendIt() {
    if (this.file.files !== null && this.file.files.length > 0) {
      const fileInfo = this.file.files[0]

      if (typeof fileInfo !== 'undefined' && fileInfo.size > CERT_EXEMPT_MAX_FILE_SIZE) {
        this.errorText.style.display = 'inline-block'
        this.file.value = ''
        return
      }

      this.errorText.style.display = 'none'
      this.uploadBtn.disabled = true
      this.uploadBtnText.textContent = this.processingAnime.getAttribute('data-text') || ''
      this.processingAnime.style.display = 'inline-block'
      this.formElement.submit()
    }
  }
}

@controller
class CertificateUploadRemoveElement extends HTMLElement {
  @target formElement: HTMLFormElement
  @target removeBtn: HTMLButtonElement
  @target removeBtnText: HTMLButtonElement
  @target processingAnime: HTMLElement

  connectedCallback() {
    this.processingAnime.style.display = 'none'
  }

  disconnectedCallback() {
    this.removeBtn.disabled = false
    this.removeBtnText.textContent = this.removeBtnText.getAttribute('data-text')
    this.processingAnime.style.display = 'none'
  }

  destroyIt() {
    this.removeBtn.disabled = true
    this.removeBtnText.textContent = this.processingAnime.getAttribute('data-text') || ''
    this.processingAnime.style.display = 'inline-block'
    this.formElement.submit()
  }
}
