import {controller, target, targets} from '@github/catalyst'
import {dialog} from '@github-ui/details-dialog'
import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'

@controller
class AzureSubscriptionDialogTriggerElement extends HTMLElement {
  private get anchorNameAttribute(): string {
    return this.getAttribute('anchor-name')!
  }

  private urlAnchor(): string {
    return window.location.hash.slice(1)
  }

  private get hasUrlAnchor(): boolean {
    return !!this.urlAnchor()
  }

  private closedDialog(anchorName: string) {
    window.location.href = window.location.href.replace(`#${anchorName}`, '')
  }

  connectedCallback() {
    if (this.urlAnchor() === this.anchorNameAttribute) {
      this.dialog()
    }
  }

  async dialog() {
    const src = this.getAttribute('src')!

    if (!this.hasUrlAnchor) {
      window.location.href = `${window.location.href}#${this.anchorNameAttribute}`
    }

    const dialogInstance = await dialog({
      content: fetchSafeDocumentFragment(document, src),
      dialogClass: 'Box-overlay--wide',
    })
    dialogInstance.addEventListener('dialog:remove', () => {
      this.closedDialog(this.anchorNameAttribute)
    })
  }
}

@controller
class AzureSubscriptionDialogElement extends HTMLElement {
  @targets boxes: HTMLElement[]
  @target submitButton: HTMLButtonElement
  @targets entries: HTMLInputElement[]
  @target confirmSelectionCheckbox: HTMLInputElement

  private get hasConfirmSelectionCheckbox(): boolean {
    return !!this.confirmSelectionCheckbox
  }

  connectedCallback() {
    // Make sure button is enabled in case we have something pre-checked (e.g. for edit)
    for (const element of this.entries) {
      if (element.checked && this.hasConfirmSelectionCheckbox) {
        this.confirmSelectionCheckbox.disabled = false
        this.submitButton.disabled = true
      } else if (element.checked) {
        this.submitButton.disabled = false
      }
    }
  }

  confirmSelection(event: Event) {
    const input = event.currentTarget as HTMLInputElement
    if (input.checked) {
      this.submitButton.disabled = false
    } else {
      this.submitButton.disabled = true
    }
  }

  toggle(event: Event) {
    const fetchFailed = this.submitButton.getAttribute('data-fetch-failed') === 'true'
    if (!fetchFailed) {
      if (this.hasConfirmSelectionCheckbox) {
        this.confirmSelectionCheckbox.disabled = false
        this.submitButton.disabled = true
      } else {
        this.submitButton.disabled = false
      }
    }

    const input = event.currentTarget as HTMLInputElement

    // remove the blue background from the last selected element
    for (const element of this.boxes) {
      const divWrapper = element.closest<HTMLElement>('div')
      if (divWrapper) {
        divWrapper.classList.add('color-bg-subtle')
        divWrapper.classList.remove('color-bg-accent')
      }
    }

    // add the blue background to the newly selected element
    const divWrapper = input.closest<HTMLElement>('div')
    if (divWrapper) {
      if (input.checked) {
        divWrapper.classList.add('color-bg-accent')
        divWrapper.classList.remove('color-bg-subtle')
      }
    }
  }

  submitTenantAction(): void {
    for (const tenant of this.entries) {
      if (tenant.checked) {
        const redirectUrlWithQueryString = tenant.getAttribute('data-redirect-url')
        const [redirectUrl, queryString] = redirectUrlWithQueryString?.split('?') ?? []
        if (redirectUrl) {
          document.location.href = encodeURI(redirectUrl) + (this.isEncoded(queryString) ? `?${queryString}` : '')
        }
        return
      }
    }
  }

  isEncoded(string: string | undefined): boolean {
    return (
      typeof string == 'string' &&
      !string.includes('<') &&
      !string.includes('>') &&
      decodeURIComponent(string) !== string
    )
  }
}
